export const prettyPrice = (price) => {
    if (price == null) {
        return '-';
    }
    return price
        .toString()
        .replace(/(\d+)(\.|,)(\d+)/g, function(o, a, b, c){
        //  o =   , a, b   =   1-, 2-  3- .
        //  \d     .     "  "
        return a + b + c.slice(0,2);
      })
        .replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ') //  
}